            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model.trim="queryInfo.query" clearable @clear="getOrderList">
                        <el-button slot="append" icon="el-icon-search" @click="queryOrderList" :disabled="queryInfo.query ? false : true"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="orderList" style="width: 100%" border stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column  prop="order_number" label=""></el-table-column>
                <el-table-column prop="order_price" label=""></el-table-column>
                <el-table-column prop="order_pay" label="">
                    <template slot-scope="scope">
                        <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
                        <el-tag type="success" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="is_send" label=""></el-table-column>
                <el-table-column prop="create_time" label="">
                    <template slot-scope="scope">
                        {{scope.row.create_time | dateFormatter}}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="addressVisible = true"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox(scope.row.order_number)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[2, 3, 5, 10]" 
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total" background>
            </el-pagination>
            <pre>{{orderList}}</pre>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressDialogClosed">
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="120px">
                <el-form-item label="/" prop="address1">
                    <el-cascader v-model="addressForm.address1" :options="cityData" clearable></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addressConfirm"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="progressVisible" width="50%">
            <el-timeline>
                <el-timeline-item
                v-for="(activity, index) in progressInfo"
                :key="index"
                :color="index === 0 ? '#00ff00' : ''"
                :timestamp="activity.time">
                {{activity.context}}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </div>
</template>

<script>
import cityData from './citydata.js'

export default {
    data() {
        return {
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 10
            },
            total: 0,
            // 
            orderList: [],
            // /
            cityData,
            // 
            cascaderProps: {
                expandTrigger: 'hover'
            },
            // 
            addressVisible: false,
            addressForm: {
                address1: [],
                address2: ''
            },
            addressFormRules: {
                address1: [{ required: true, message: '', trigger: 'change' }],
                address2: [{ required: true, message: '', trigger: 'blur' }]
            },
            // 
            progressVisible: false,
            // 
            progressInfo: []
        }
    },
    created() {
        this.getOrderList()
    },
    methods: {
        // 
        async getOrderList() {
            const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
            if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
            this.$message.success('')
            this.orderList = res.data.goods
            this.total = res.data.total
        },
        // 
        queryOrderList() {
            this.queryInfo.pagenum = 1
            this.getOrderList()
        },
        // pagesize 
        handleSizeChange(size) {
            console.log('size: ', size);
            this.queryInfo.pagesize = size
            const maxN = parseInt(this.total / this.queryInfo.pagesize + '') + (this.total % this.queryInfo.pagesize > 0 ? 1 : 0)
            this.queryInfo.pagenum = this.queryInfo.pagenum > maxN ? maxN : this.queryInfo.pagenum
            this.getOrderList()
        },
        //  
        handleCurrentChange(num) {
            console.log('num: ', num);
            this.queryInfo.pagenum = num
            this.getOrderList()
        },
        // 
        addressDialogClosed() {
            // console.log('addressForm: ', this.addressForm);
            this.$refs.addressFormRef.resetFields()
        },
        // 
        addressConfirm() {
            // console.log('addressForm: ', this.addressForm);
            this.addressVisible = false
        },
        // 
        async showProgressBox(orderNumber) {
            // const { data: res } = await this.$http.get('kuaidi/' + orderNumber)
            // if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
            // this.$message.success('')
            // this.progressInfo = res.data
            
            const data = `[
                {
